body {
   margin: 0;
}
.edit, .hide {
   position: absolute;
   right: 20px;
   bottom: 20px;
   background-color: white;
   border: none;
   border-radius: 50%;
   box-shadow: 4px 4px 18px -6px rgba(0,0,0,0.75);
   padding: 10px;
   cursor: pointer;
   transition: .10s ease-in-out;
   z-index: 2;
   outline: none;
}
.edit img, .hide img {
   width: 50px;
   height: 50px;
}
.edit:hover, .hide:hover {
   transform: scale(1.1);
}
.hide {
   right: 110px;
}

.modal {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   box-shadow: 4px 4px 18px -6px rgba(0,0,0,0.75);
   padding: 30px;
   padding-top: 50px;
   background-color: white;
   z-index: 2;
}
.modal .close {
   position: absolute;
   top: 20px;
   right: 20px;
   background: none;
   border: none;
   font-weight: bold;
   background-color: #d3d3d3;
   width: 30px;
   height: 30px;
   font-size: 20px;
   border-radius: 50%;
   outline: none;
   cursor: pointer;
   transition: .1s ease-in-out;
}
.modal .close:hover {
   background-color: blue;
   color: white;
}
form ul {
   list-style-type: none;
   padding: 0;
   display: flex;
   justify-content: space-between;
}
form ul li {
   display: flex;
   flex-direction: column;
   margin: 0 10px 10px;
}
.input {
   padding: 0 20px;
}
.input label {
   display: block;
   padding-bottom: 2px;
}
.input input {
   border: 1px solid #8f8f8f;
   padding: 5px 10px;
}
.submit {
   background: none;
   border: none;
   font-weight: bold;
   background-color: #d3d3d3;
   font-size: 20px;
   padding: 10px;
   margin-left: 10px;
   transition: .1s ease-in-out;
   cursor: pointer;
   margin-top: 20px;
}
.submit:hover {
   background-color: blue;
   color: white;
}

.canvas {
   width: calc(100% - 300px);
   height: 90%;
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   left: 40px;
}

#chart {
   position: absolute;
   width: 100%;
   height: 100%;
   display: flex;
   justify-content: center;
   align-items: center;
   border: 2px solid black
}

.colors {
   width: 100px;
   position: absolute;
   right: 40px;
   top: 50%;
   transform: translateY(-50%);
   display: flex;
   flex-direction: column;
   z-index: 0;
}
.colors h6 {
   position: absolute;
   top: -30px;
   left: 50%;
   transform: translateX(-50%);
   font-size: 18px;
   margin: 0;
}
.colors button {
   position: absolute;
   top: -80px;
   left: 50%;
   transform: translateX(-50%);
   border: none;
   font-weight: bold;
   background-color: #d3d3d3;
   font-size: 20px;
   padding: 10px;
   transition: .1s ease-in-out;
   cursor: pointer;
   outline: none;
}
.colors button:hover {
   background-color: blue;
   color: white;
}
.color-item {
   height: 50px;
   position: relative;
}
.color-item span {
   position: absolute;
   top: 50%;
   left: -10px;
   transform: translate(-100%, -50%);
}
.hr {
   width: 110px;
   height: 6px;
   border-radius: 3px;
   position: absolute;
   background-color: black;
   cursor: pointer;
   margin-left: -5px;
   margin-top: -2px;
   z-index: 2;
}

#overlay {
   z-index: 9999;
   cursor: nw-resize;
   width: 80vw;
   height: 600px;
   position: absolute;
   top: 50%;
   transform: translateY(-50%);
   left: 40px;
}

#rects {
   position: absolute;
   width: 100%;
   height: 100%;
   top: 0;
   left: 0;
}